
// ----- Usuario de foro ----- //
.user-foro {
  display       : flex;
  margin-bottom : 0.5rem;

  &__image {
    @include profile-photo;
    margin-right : 0.75rem;
  }

  // Contenedor de nombre y fecha de la publicacin
  &__info {
    display        : flex;
    flex-direction : column;
    line-height    : initial;
  }

  &__name {
    color       : #53575A;
    font-weight : bold;
  }

  &__date {
    color : #BABDBF;
  }

}
