  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.ed-menu.s-horizontal {
  width: 100%;
  display: flex;
  align-items: center;
}

.ed-menu.s-horizontal, .ed-menu.s-horizontal ul {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

